* {
  background-color: rgb(43, 43, 43);
  color: rgb(43, 43, 43);
  margin: 0;
  padding: 0;
  border: 0;
  border-radius: 0.7em;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

#destination-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 0;
  list-style-type: none;
}
.destination {
  margin: 1em;
  /* padding: 1em; */
  border-width: 0.7em;
  border-top-width: 2em;
  border-color: #ebe2d6;
  background-color: #ebe2d6;
  border-style: solid;
  font-size: 1rem;
}

.destination-img {
  width: 20em;
  height: 20em;
  /* border-left: 1em;
  border-top: 1em;
  border-right: 1em; */
  /* border-color: rgba(255, 156, 43, 0.885); */
  /* border-style: solid; */
  object-fit: cover;
  border-radius: 0.3em;
  filter: grayscale(100%) opacity(50%);
}

h1 {
  background-color: tan;
  font-size: 3rem;
  padding: 0.5em;
  border-radius: 0;
}

h3 {
  margin: 0;
  margin-top: 0.25em;
  font-size: 1.5rem;
  border-radius: 0;
  background-color: #ebe2d6;
}

.visited {
  filter: invert(0%) grayscale(0%);
}

#total-visited {
  margin: 0;
  margin-top: 2.5em;
  margin-bottom: 1.5em;
  background-color: grey;
  width: 100%;
  border-radius: 0;
  padding: 1em;
  color: rgb(27, 27, 27);
}
